// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Lib from '@/assets/js/Lib';
import { Toast } from 'mint-ui';
// require('@/assets/css/index.css');
// import'@/assets/css/index.css';
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  data() {
    return {
      busId: 0,
      url: '',
      currUrl: '',
      urlCardNo: '',
      urlShare: '',
      shareTitle: '!',
      shareDesc: '',
      shareImg: 'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
    }
  },
  watch: {
    //  $route 
    "$route" (to, from) {
      let currentRouter = this.$router.currentRoute.fullPath; //
      console.log('hdz',window.location.href)
      console.log('currentRouter', currentRouter);
      if (currentRouter.indexOf('qrcode') == -1) { //userShare,

      } else {
        this.loadData(); //userShare      
      }
    }
  },
  methods: {
    loadData: function() {
      var _this = this,
        json = {};
      _this.busId = _this.$route.params.busId;
      _this.currUrl = window.location.href;

      _this.urlShare = memberAPI.activeAPI.goURL + memberAPI.activeAPI.goMen + '?#/shareQRCode/' + _this.busId;
      // _this.currUrl = memberAPI.activeAPI.goURL + memberAPI.activeAPI.goMen + '#/qrcode/' + _this.busId;
      alert(_this.currUrl)
      json["busId"] = _this.busId;
      json["requestUrl"] = _this.currUrl;
      console.log("_this.urlShare", json);
      Lib.M.ajax({
          'url': memberAPI.activeAPI.wxshareCard_POST,
          'type': 'POST',
          'data': {
            "json": json
          },
          'success': function(data) {
            // alert(JSON.stringify(data))
            console.log('shareQRCode', data)
            if (data.code == 0) {
              wx.config({
                debug: true, // ,apialertpclogpc
                appId: data.data.appid, // 
                timestamp: data.data.timestamp, // 
                nonceStr: data.data.nonce_str, // 
                signature: data.data.signature, // 1
                jsApiList: ["onMenuShareAppMessage"] // JSJS2
              });
              wx.ready(function() {
                //
                alert( _this.urlShare);
                wx.onMenuShareAppMessage({
                  title: _this.shareTitle, // 
                  desc: '123', // 
                  link: _this.urlShare, // 
                  imgUrl: _this.shareImg, // 
                  success: function() {
                    // 
                    Toast({
                      message: '',
                      duration: 2000
                    });
                  },
                  cancel: function() {
                    // 
                    Toast({
                      message: '',
                      duration: 1000
                    });
                  },
                  error: function(res) {
                    alert(res)
                  }
                });
              });
              wx.error(function(res) {
                  alert ('err', res)
              });
          }
        }
      })
  }
},
components: { App }
})



